
* {
    box-sizing: border-box
}
a:link,
a:visited,
a:active {
    color: #333;
}
i {
    font-style: normal;
}
h2 {
    font-weight: 400;
}
.f-brk {
    word-wrap: break-word;
    word-break: break-all;
    white-space: normal;
}
.f-thide {
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}
.f-single {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.text_hidden {
    overflow: hidden;
    text-overflow: ellipsis;
}
.font-icon {
    font-family: 'iconfont';
    color: #fff;
}
.vux-header .vux-header-title {
    margin: 0 66px;
}
.remd_num {
    position: absolute;
    right: 5px;
    top: 2px;
    font-size: 12px;
    color: #fff;
    text-shadow: 1px 0 0 rgba(0, 0, 0, .15);
    z-index: inherit 2;
    padding-left: 15px;
    background-size: 11px 10px;
    background-repeat: no-repeat;
    background-position: 0 4px;
}
.u-earp {
    background-image: url(./assets/icon/icon_music.svg);
}
.player-view{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all .5s;
    transition: all .5s;
}



